<template>
  <div class="upIcons">
    <div class="iconMain">
      <van-row gutter="20" class="iconAll">
        <van-col span="12">
          <img
            :src="require('../../assets/img/user/icon1.png')"
            alt=""
          />
        </van-col>
        <van-col span="12">
          <img
            :src="require('../../assets/img/user/icon2.png')"
            alt=""
          />
        </van-col>
        <van-col span="24">
            <div class="info">
                该头像将用于工地考勤，请确保上传的是您自己的照片，且人脸大小占整张照片1/3以上
            </div>
        </van-col>
      </van-row>
    </div>
    <div class="upBtns">
        <van-uploader style="width:100%" :before-read="beforeRead" :after-read="afterRead">
            <van-button type="primary" color="#48BED0" block round>点击上传照片</van-button>
        </van-uploader>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      uploadImage: {
        imgByte: "",
        imgType: "",
      },
    };
  },
  methods: {
    // 返回布尔值
    beforeRead(file) {
      if (
        file.type != "image/jpeg" &&
        file.type != "image/png" &&
        file.type != "image/jpg"
      ) {
        this.$toast("请上传 jpg,png 格式图片");
        return false;
      }
      this.uploadImage.imgType = file.type;
      return true;
    },
    afterRead(file) {
      this.uploadImage.imgByte = file.content;
      this.$toast.loading({
          message: '上传中...',
        duration: 0, // 持续展示 toast
      })
      this.$http.jsb.uploadImage(this.uploadImage).then((res) => {
        if(res.code == 200){
            this.$toast.clear();
            this.$toast.success('上传成功\n等待系统审核')
        }
      });
    },
  },
};
</script>

